import React, {useEffect, useRef} from "react";
import './card1.css';


type Props = {
  children: React.ReactNode;
  maxTilt?: number;
  scale?: number;
  speed?: number;
};

const Card1 = ({ children, maxTilt = 15, scale = 1.05, speed = 300 }: Props) => {
  const ref = useRef<HTMLDivElement | null>(null);
  const requestRef = useRef<number | null>(null);
  const tiltData = useRef({ rotateX: 0, rotateY: 0 });

  const handleMouseMove = (e: React.MouseEvent<HTMLDivElement>) => {
    const el = ref.current;
    if (!el) return;

    const rect = el.getBoundingClientRect();
    const x = e.clientX - rect.left - rect.width / 2;
    const y = e.clientY - rect.top - rect.height / 2;

    const rotateX = (-y / (rect.height / 2)) * maxTilt;
    const rotateY = (x / (rect.width / 2)) * maxTilt;

    tiltData.current = { rotateX, rotateY };

    if (requestRef.current === null) {
      requestRef.current = requestAnimationFrame(applyTilt);
    }
  };

  const applyTilt = () => {
    const el = ref.current;
    if (el) {
      const { rotateX, rotateY } = tiltData.current;
      el.style.transform = `
        perspective(1000px)
        rotateX(${rotateX.toFixed(2)}deg)
        rotateY(${rotateY.toFixed(2)}deg)
        scale(${scale})
      `;
    }
    requestRef.current = null;
  };

  const resetTilt = () => {
    const el = ref.current;
    if (el) {
      el.style.transform = `
        perspective(1000px)
        rotateX(0deg)
        rotateY(0deg)
        scale(1)
      `;
    }
  };

  useEffect(() => {
    return () => {
      if (requestRef.current) {
        cancelAnimationFrame(requestRef.current);
      }
    };
  }, []);
  

  return (
    <div
      className="tilt-card1"
      style={{ transition: `transform ${speed}ms ease` }}
      ref={ref}
      onMouseMove={handleMouseMove}
      onMouseLeave={resetTilt}
    >

      <div className="tilt-card1-inner">
        {children}
      </div>

    </div>
  );
};

export default Card1;